.countries{
    width: 95%;
    margin : 0 auto;

    .cards {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
        
    .filter{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 91%;
        margin: 42px auto;

        input, select{
            height: 55px;
            border: none;
            border-radius: 5px;
            box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.139);
            
            &.dark{
                color: hsl(0, 0%, 100%);
                background: hsl(209, 23%, 22%);

                &::placeholder{
                    color: hsl(0, 0%, 100%);;
                }
            }

            &:focus{
                outline-width: 0;
            }
        }

        .input img{
            position: relative;
            top: 7px;
            left: -450px;
        }

        input{
            width: 480px;
            padding: 24px 72px;
            
            &::placeholder{
                color: hsl(0, 0%, 52%);
            }
        }
        
        .select img{
            position: relative;
            top: 7px;
            left: -45px
        }
        select{
            width: 300px;
            padding: 12px;
            appearance: none;
        }
    }
}

@media all and (max-width: 960px) {
    .countries{

        .filter{
            flex-direction: column;
            align-items: flex-start;

            select{
                margin-top: 36px;
            }
            input, .input{
                width: 100%;
            }

            .input img{
                position: relative;
                top: -40px;
                left: 35px;
            }
        }
    }
}